/* ------------- corpo da calculadora ------------- */
.calculadora {
  padding-top: 5vh;
  height: 320px;
  width: 235px;
  border-radius: 5px;
  display: grid;
  grid-template-columns: repeat(4, 25%);
  grid-template-rows: 1fr repeat(5, 48px);
  margin: auto;
}
.span-3 {
  grid-column: span 3;
}
.span-2 {
  grid-column: span 2;
}

/* ------------- display ------------- */
.display {
  grid-column: span 4;
  background-color: #0004;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  padding: 20px;
  font-size: 1.8rem;
}

