  color: rgb(46, 46, 46);
  border: none;
  border-right: var(--border-button);
  border-bottom: var(--border-button);
  outline: none;
}
.button:active {
  opacity: 0.7;
}

.button.op {
  background-color: dodgerblue;
  color: white;
